<template>
  <div class="cms-list-layout">
    <cms-tabs>
      <cms-tab to="/recharge/recharge">充值</cms-tab>
      <cms-tab to="/recharge/present">提现</cms-tab>
    </cms-tabs>
    <cms-prompt collapse="true">
      <li>显示所有会员充值记录及提现申请</li>
      <li>可以对充值记录更改状态及提现申请审核</li>
    </cms-prompt>
    <section class="cms-list-query">
      <div class="query-group">
        <label>会员账号</label>
        <el-input v-model="queryParams.username" @keyup.native.enter="query"></el-input>
      </div>
      <div class="query-group">
        <label>状态</label>
        <el-select v-model="queryParams.status" @change="query">
          <el-option label="全部" value></el-option>
          <el-option label="待支付" :value="'1'"></el-option>
          <el-option label="未完成" :value="'2'"></el-option>
          <el-option label="已完成" :value="'3'"></el-option>
          <el-option label="已取消" :value="'4'"></el-option>
        </el-select>
      </div>
      <div class="query-group">
        <label>支付方式</label>
        <el-select v-model="queryParams.payTypeCode" @change="query">
          <el-option label="全部" value></el-option>
          <el-option label="支付宝" :value="'1'"></el-option>
          <el-option label="微信" :value="'2'"></el-option>
          <el-option label="银联" :value="'3'"></el-option>
        </el-select>
      </div>
      <div class="query-group">
        <cms-button size="small" :colorPicker="true" @click="query">查询</cms-button>
      </div>
    </section>
    <cms-sticky>
      <section class="cms-batch">
        <cms-button icon="error" type="error" size="small" @click="deleteDatas(ids)">批量删除</cms-button>
      </section>
    </cms-sticky>
    <el-table
      :data="tableData"
      style="width: 100%"
      stripe
      v-loading="loading"
      @selection-change="getCheckItems"
    >
      <el-table-column type="selection" width="46" fixed="left"></el-table-column>
      <el-table-column prop="businessNo" label="充值编号" min-width="200"></el-table-column>
      <el-table-column prop="member.username" label="会员账户" min-width="100"></el-table-column>
      <el-table-column prop="money" label="金额" min-width="100" align="center">
        <template slot-scope="scope">{{$cms.float(scope.row.money)}}</template>
      </el-table-column>
      <el-table-column prop="payTypeCode" label="支付方式" min-width="100" align="center">
        <template slot-scope="scope">
          <span v-if="scope.row.payTypeCode == '1'">支付宝</span>
          <span v-else-if="scope.row.payTypeCode == '2'">微信</span>
          <span v-else-if="scope.row.payTypeCode == '3'">银联</span>
          <span v-else-if="scope.row.payTypeCode == '4'">余额</span>
        </template>
      </el-table-column>
      <el-table-column prop="status" label="状态" min-width="110" align="center">
        <template slot-scope="scope">
          <span v-if="scope.row.status == '1'" class="info">待支付</span>
          <span v-else-if="scope.row.status == '2'" class="error">未完成</span>
          <span v-else-if="scope.row.status == '3'" class="success">已完成</span>
          <span v-else-if="scope.row.status == '4'" class="error">已取消</span>
        </template>
      </el-table-column>
      <el-table-column prop="createTime" label="充值时间" min-width="170" align="center">
        <template slot-scope="scope">{{$moment(scope.row.createTime)}}</template>
      </el-table-column>
      <el-table-column prop="accountDetails.tradeNo" label="交易流水号" min-width="300"></el-table-column>
      <el-table-column prop="finishTime" label="交易完成时间" min-width="170" align="center">
        <template slot-scope="scope">{{$moment(scope.row.finishTime)}}</template>
      </el-table-column>
      <el-table-column prop="remark" label="备注" min-width="200"></el-table-column>
      <el-table-column label="操作选项" width="155" fixed="right" align="center">
        <div slot-scope="scope">
          <cms-button
            icon="error"
            size="small"
            type="info"
            v-if="scope.row.status === 4"
            @click="deleteData(scope.row.id)"
          >删除</cms-button>
          <cms-button
            icon="search"
            size="small"
            type="info"
            v-if="scope.row.status === 1"
            @click="queryPayStatus(scope.row.id)"
          >查询交易结果</cms-button>
        </div>
      </el-table-column>
    </el-table>
    <cms-pagination @change="getPage" :total="total"></cms-pagination>
  </div>
</template>
<script>
import {
  rechargeList,
  rechargeDelete,
  queryResult
} from '@/api/users/recharge.js'
import list from '@/mixins/list'
var defaultData = {
  username: '',
  status: '',
  payTypeCode: '',
  page: 1,
  size: 10
}
export default {
  mixins: [list],
  data() {
    return {
      queryParams: Object.assign({}, defaultData),
      value: ''
    }
  },
  methods: {
    deleteData(id) {
      // 包装一层删除的操作
      this.deleteTableData(rechargeDelete, id) // 这个才是真实的删除方法
    },
    deleteDatas(ids) {
      this.deleteTableDatas(rechargeDelete, ids) // 这个才是真实的删除方法
    },
    queryPayStatus(id) {
      queryResult({ id: id }).then(res => {
        if (res.code === 200) {
          this.getTableData(rechargeList, this.queryParams)
        } else {
          this.$errorMessage(res.message)
        }
      })
    }
  },
  created() {
    this.getTableData(rechargeList, this.queryParams)
  }
}
</script>

<style>
</style>



