// 充值/提现申请相关接口
import axios from './../fetch'

// 充值记录列表分页
export const rechargeList = params => { return axios.get('/admin/platform/memberrecharge/page', { params: params }) }

// 充值记录删除
export const rechargeDelete = params => { return axios.post('/admin/platform/memberrecharge/delete', params) }

// 提现记录列表分页
export const presentList = params => { return axios.get('/admin/platform/memberpresent/page', { params: params }) }

// 提现记录删除
export const presentDelete = params => { return axios.post('/admin/platform/memberpresent/delete', params) }
// 提现审核通过
export const presentPass = params => { return axios.post('/admin/platform/memberpresent/pass', params) }
// 提现审核不通过
export const presentReject = params => { return axios.post('/admin/platform/memberpresent/reject', params) }
// 提现审核不通过
export const queryResult = params => { return axios.get('/admin/platform/memberrecharge/queryResult', { params: params }) }



